import { defineComponent } from "vue";
import { formatNumber, formatMoney } from "@/utils";
export default defineComponent({
    name: "GenericModal",
    props: {
        id: String,
        name: String,
        assets: Array,
    },
    computed: {
        assetsSorted() {
            if (!this.assets) {
                return [];
            }
            return this.assets.sort((a, b) => {
                if (a.amount * a.price < b.amount * b.price)
                    return 1;
                else
                    return -1;
            });
        },
    },
    methods: { formatNumber, formatMoney },
});
